.GNB_master{
    width: 100vw;
    border-bottom: 1px solid rgba(0,0,0,.08);
}
.GNB{
}
.GNB_wrapper{
    background-color: white;;
    position: relative;
}
.GNB_wrapper_inner{
    width: 1150px;
    height: 70px;
    background-color: white;
    display: flex;
    position: relative;
    margin: 0 auto;
}
.kasper_logo{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 40px;
}
.kasper_logo_word{
    height: 40px;
    width: 240px;
    vertical-align: middle;
    font-size: 20px;
    font-family: sans-serif;
    background: url('../Images/Logo2.png');
}
.kasper_logo_back{
    background: url('../Images/Logo1.png');
    width: 24px;
    height: 24px;
    display: block;
} 
.kasper_util_nav{
    top: 10px;
    right: 120px;
    position: absolute;
}
.kasper_util_nav > ul{
    list-style: none;
    display: flex;
    font-family:sans-serif;
    font-size: 16px;
}
.kasper_util_nav > ul > li{
    list-style: none;
    display: flex;
    padding-inline-end: 40px;
    transition: all 1s;
}
.btn_search{
    width: 50px;
    height: 50px;
    right: 90px;
    margin-top: 10px;
    position: absolute;

}
.totalSearch{
    width: 48px;
    height: 48px;
    background: url('../Images/Find.png');
    display: block;
}
.btn_sub_search{
    width: 64px;
    height: 64px;
    top: 10px;
    right: 0px;
}
.GNB_wrapper_nav{
    width: 1100px;
    height: 80px;
    margin: 0 auto;

}
.GNB_wrapper_nav > ul{
    list-style: none;
    display: flex;
    font-family:sans-serif;
    font-size: 18px;
    float: right;
    background-color: white;
    height: 80px;
    margin: 0;
}
.GNB_wrapper_nav > ul > li{    
    padding-inline-end: 30px;
    padding-inline-start: 30px;
    color: black;
    -webkit-transition: color 0.3s, background-color 0.3s;
    transition: color 0.3s, background-color 0.3s;
    height: 80px;
    width: 110px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.GNB_wrapper_nav > ul > li:hover{    
    color: white;
    background-color: #FED14B;
}
.GNB_all{
    width: 1150px;
    height: 400px;
    background-color: #FED14B;
    margin:0 auto;
    position: absolute;
    z-index: 9999;
    top: 150px;
    display: none;
}

.totalLabel{
    display: none;
}